.PortfolioHeader {
  width: 90%;
  height: 70px;
  padding: 20px;
  margin: 20px;
  position: relative;
}

/* Images */

.PorfolioImages {
  /* margin: 20px; */
  border-radius: 5px;
  z-index: -100;
  position: relative;
}

.Images {
  margin: 20px;
  max-width: 90%;
  border-radius: 5px;
  max-height: 500px;
  z-index: -100;
  position: relative;
}

/* Pillar Based Sections and Pages */

.PillarRow {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
  position: relative;
}

.PillarSection {
  width: 100%;
  margin: 0;
  padding: 10px;
  /* background-color: #4372c4; */
  background-color: white;
  opacity: 0.95;
  height: 90%;
}

.ParBox {
  font-size: 1.2em;
  height: 30%;
  max-width: 70%;
  width: 600px;
  align-items: center;
  justify-content: center;
  background-color: white;
  padding: 10px;
  border-radius: 5px;
  opacity: 0.95;
  border: 1px solid;
  border-color: #c1c9d8;
  opacity: 0.7;
}

.Pillar {
  display: flex;
  flex-direction: column;
  width: 15%;
  min-width: 150px;
  padding: 5px;
  margin: 20px;
  background-color: white;
  border-radius: 5px;
  border: 1px solid;
  border-color: #c1c9d8;
}

.PillarTop {
  border-radius: 5px 5px 0 0;
  height: 60px;
  width: 100%;
  background-color: #4372c4;
  align-items: center;
  justify-content: center;
  display: flex;
  padding: 0;
  margin: 0;
}

.PillarBackground {
  height: 270px;
  background-color: #c1c9d8;
  padding: 0;
  margin: 0;
  min-width: 150px;
}

.PillarBullets {
  text-align: left;
  padding: 0;
  padding-left: 25px;
  padding-right: 5px;
  margin: 10px 0;
}

/* BubbleCard */

.BubbleCards {
  /* min-width: 400px;
  min-height: 500px; */
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
  padding: 10px;
  margin: 10px;
}

.BubbleCard {
  position: relative;
  height: 150px;
  width: 300px;
  text-align: center;
  border: 100px;
  /* background-color: red; */
  align-items: center;
  margin: 0;
  padding: 0;
}

.BubbleTop {
  position: absolute;
  color: #ffffff;
  background-color: #4372c4;
  border-radius: 15px;
  width: 200px;
  height: 30px;
  margin: 0 auto;
  padding: 0;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
}

.BubbleBottom {
  top: 28px;
  position: absolute;
  border-radius: 5px;
  /* margin-top: 0px; */
  height: 100px;
  width: 300px;
  border: 1px solid;
  border-color: #c1c9d8;
  padding: 0;
  margin: 0;
  text-align: center;
  align-items: center;
  justify-content: space-evenly;
  display: flex;
}

/* Contacts */

.cards {
  min-width: 400px;
  min-height: 500px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
  padding: 10px;
  margin: 10px;
}

.card {
  border-radius: 5px;
  position: relative;
  padding: 15px 15px;
  background-color: #c1c9d8;
  margin: 50px 0;
  height: 200px;
  box-shadow: 0 2px 5px #ccc;
  text-align: left;
  min-width: 300px;
  opacity: 0.95;
}

.top {
  border-radius: 5px 5px 0 0;
  height: 100px;
  width: 100%;
  background-color: #4372c4;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
}

.name {
  font-size: 1.5em;
  color: #ffffff;
  display: flex;
  flex: 1;
  margin: 10% 10px 0;
}

.card img {
  margin: 30px 10px 0 0;
}

.circle-img {
  border-radius: 50%;
  border: 7px solid #fff;
  width: 120px;
  height: 120px;
}

.bottom {
  margin-top: 120px;
}

.info {
  margin: 10px 0;
  color: #2d3436;
}

.BackgroundImage {
  height: 400px;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
}

/* Counter */

.Counter {
  background: #333;
  text-align: center;
  width: 60px;
  border-radius: 50%;
}
