.AppSection {
  height: 150px;
  width: 100%;
  align-items: center;
  justify-content: center;
}
/*  */
.App-header {
  margin: 10px;
  width: 98%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  align-items: center;
  justify-content: space-between;
}
.App-logo {
  height: 70px;
  pointer-events: none;
}

.NavBar {
  min-width: 350px;
  padding: 15px;
  background-color: #c1c9d8;
  border-radius: 5px;
  display: flex;
  flex-direction: row;

  justify-content: space-between;
  text-align: center;
  align-items: center;
  color: black;
}

.NavBar:hover {
  padding: 20px;
  box-shadow: 0 2px 5px #ccc;
}

/* .NavLink {
  background-color: black;
  color: white;
  border-radius: 5px;
} */

.ActiveNavLink {
  background-color: white;
  color: black;
  border-radius: 5px;
  padding: 3px;
}

.AppFooter {
  text-align: center;
  align-items: center;
  justify-content: center;

  background-color: #c1c9d8;
  max-width: 100%;
  margin: 5px 0 0;
  padding: 1px;
  border: 1px solid;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-zoom {
  0% {
    top: 0px;
    width: 10px;
  }
  100% {
    top: 100px;
    width: 200px;
  }
}
